:root {
  --blanco: #ffff;
  --gris: #cccc; 
  --negro: rgb(0, 0, 0.5);
  --sombra: 3px 5px 2px 0px rgba(55, 46, 48, 0.17);

  --azulTemplate: #303C54;
  --azulTemplate2: #324364;

  --azul1: #2d2a8c;
  --azul2: #3854a6;
  --azul3: #3365a6;
  --azul4: #368abf;
  --azul5: #48b0d9;
}

.contenedor-cabecera {
  background-color: var(--azulTemplate);
  color: var(--blanco);
  font-size: .8rem;
}

.contenedor-cabecera-principal {
  background-color: var(--azulTemplate);
  color: var(--blanco);
  font-size: 1rem;
}
.contenedor-cabecera-grafica {
  background-color: var(--blanco);
  color: var(--azulTemplate);
  font-size: .8rem;
}
.contenedor-btn{
  display: grid;
  place-items: center;
}
.btn-guardar{
  padding-left: 2rem;
  padding-right: 2rem;
  background-color: transparent;
  border: var(--azulTemplate) solid 1px;
  color: var(--azulTemplate);
}
.btn-guardar:hover{
  background-color: var(--azulTemplate);
  color: var(--blanco);
}

.icono {
  width:48px;
  height:48px;
}
li{
  font-size: 0.9rem;
}

span {
  margin-left: 1rem;
  font-size: 20px;
  font-weight: 600;
}

pre {
  margin-top: 1rem;
}