/*Reportes */
:root {
    --blanco: #ffff;
    --gris: #cccc;
    --AzulTemplate: #303c54;
    --negro: rgb(0, 0, 0.5);
    --sombra: 3px 5px 2px 0px rgba(55, 46, 48, 0.17);
    --azul1: #2d2a8c;
    --azul2: #3854a6;
    --azul3: #3365a6;
    --azul4: #368abf;
    --azul5: #48b0d9;
  }
.contenedor-cabecera {
    background-color: var(--azul2);
    color: var(--blanco);
    font-size: .8rem;
}
  
